button {
    box-shadow: 0px 1px 0px 0px #80867d;
	border-radius:3px;
	border:1px solid gray;
	color:#f8f1f1;
	text-align: left;
	font-size:12px;
	font-weight: bold;
	padding:15px 25px;
	height: 60%;
	width: 70%;
	line-height: 30%;
}

.arrivedColor {
	color: #1bb977;
	background-color: transparent;
	border:1px solid #1bb977;
}
.callColor {
	color:#f8f1f1;
	background-color: #1bb977;
}

.waitingColor {
	color:#f8f1f1;
    background-color: red;
	border:1px solid red;
}
